import Navbar from "@/components/Navbar";
import Link from "next/link";
//Part1: get all the json files from blog data directory
//Part2: iterate and then display them
const blogPosts = [
  {
    id: 1,
    title: "Getting Started with Next.js",
    excerpt:
      "A beginner-friendly guide to setting up your first Next.js project and understanding the basics of server and client components.",
    author: "Hanzala",
    date: "Sept 10, 2025",
  },
  {
    id: 2,
    title: "Mastering Tailwind CSS",
    excerpt:
      "Learn how to create beautiful, responsive designs faster using Tailwind’s utility-first classes.",
    author: "Guest Writer",
    date: "Sept 12, 2025",
  },
  {
    id: 3,
    title: "React Performance Tips",
    excerpt:
      "Discover key techniques like memoization, code splitting, and lazy loading to make your React apps blazing fast.",
    author: "Community",
    date: "Sept 14, 2025",
  },
];

export default function Blog() {
  return (
    <div className="font-sans min-h-screen bg-gray-50 dark:bg-gray-900 text-gray-900 dark:text-white">
      <Navbar />

      {/* Hero */}
      <header className="text-center py-12 px-6">
        <h1 className="text-4xl sm:text-5xl font-extrabold">Our Blog</h1>
        <p className="mt-3 text-lg text-gray-600 dark:text-gray-400">
          Insights, tutorials, and stories from Hunting Coder
        </p>
      </header>

      {/* Blog Grid */}
      <main className="max-w-6xl mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 py-12 px-6 auto-rows-fr">
        {blogPosts.map((post) => (
          <Link key={post.id} href={`/blog/${post.id}`} className="h-full">
            <article
              className="flex flex-col justify-between h-full border border-gray-300 dark:border-gray-700 rounded-xl p-6 bg-white dark:bg-gray-800 shadow-lg hover:shadow-2xl transition duration-300 transform hover:-translate-y-2 opacity-0 animate-fadeIn"
            >
              <div>
                <h2 className="text-2xl font-bold mb-3">{post.title}</h2>
                <p className="text-lg text-gray-600 dark:text-gray-300 mb-6">
                  {post.excerpt}
                </p>
              </div>
              <div className="text-blue-600 dark:text-blue-400 hover:underline text-sm font-bold">
                Read more →
              </div>
            </article>
          </Link>
        ))}
      </main>
    </div>
  );
}
